import { useTranslation } from "react-i18next";
import LayoutDefault from "../layouts/LayoutDefault";
import useFetchDataStore from "../stores/useFetchDataStore";
import useUsersStore from "../stores/useUsesStore";

const UsersPage = () => {
  const { t } = useTranslation();

  const { users } = useUsersStore((state) => ({
    users: state.users,
  }));

  const { callApi, getData, loading, error } = useFetchDataStore((state) => ({
    callApi: state.callApi,
    getData: state.getData,
    loading: state.loading,
    error: state.error,
  }));

  return (
    <LayoutDefault>
      <div className="flex-1 w-full">
        <section className="bg-white px-4 py-4">
          <h1 className="text-2xl font-semibold border-b pb-1 mb-4 ">
            {t("users.title")}
          </h1>
        </section>
        <section className="w-4/6 min-h-96 flex flex-col justify-around items-center mx-auto"></section>
      </div>
    </LayoutDefault>
  );
};

export default UsersPage;
